	methods: {
		toArticleDetail:function(e){
			var id = e.currentTarget.dataset.id;
			if(id > 0){
				uni.navigateTo({
					url:'../article/detail?id=' + id
				})
			}else{
				util.showToast('ID有误')
			}
		},			
	}
